import React, { Component } from 'react'
import { useForm } from 'react-hook-form'
import * as yup from 'yup'

    const schema = yup.object().shape({
        fname: yup.string().required("Please provide your first name").min(2, "Too short- should be more than 1 letter"),
        lname: yup.string().required("Please provide your last name").min(2, "Too short- should be more than 1 letter"),
        age: yup.number().typeError('age must be a number').positive('Must be greater than zero').required("Age is required").min(18, "You should be atleast 18yrs old").max(120, "You should be atmost 120yrs old"),
        email: yup.string().email().required("Required"),
        file: yup.mixed().required("You need to provide a file").test("fileSize", "The file is too large should be less than or equal to 10mb", (value) => {
            return value && value[0].size <= 10000000
        }).test("type", "We only support mp3, flac, wav", (value) => {
            return value && value[0].type === ("file.mp3" || "file.flac" || "file.wav");
        }),
    });

    function Contact() {

        const { register, handleSubmit, errors } = useForm({validationSchema: schema});

        const onSubmit = async (data) => {
            const formData = new FormData()
            formData.append("fname", data.fname)
            formData.append("lname", data.lname)
            formData.append("age", data.age)
            formData.append("email", data.email)
            formData.append("file", data.file[0])

            const res = await fetch("http://localhost:4000/formData", {
                method: "POST",
                body: formData
            }).then(res => res.json())
            alert(JSON.stringify(res))
         }

         return (
        <section className="page-section" id="contact">
        <div className="container">
            <div className="text-center">
                <h2 className="section-heading text-uppercase">Fill the Form</h2>
            </div>
            <form id="formElem" noValidate onSubmit={handleSubmit((data) => alert(JSON.stringify(data)))}>
                <div className="form-group">
                    <label>First Name</label>
                    <input type="text" name="fname" minlength="2" className="form-control" ref={register} placeholder="Your First *" required="required" data-validation-required-message="Please enter your First name"/>
                    <p class="help-block text-danger"></p>
                    {errors.fname && <p>{errors.fname.message}</p>}
                </div>
                <div className="form-group">
                    <label>Last Name</label>
                    <input ref={register} type="text" name="lname" minlength="2" className="form-control" placeholder="Your Last Name *" required="required" data-validation-required-message="Please enter your last name"/>
                    <p class="help-block text-danger"></p>
                    {errors.lname && <p>{errors.lname.message}</p>}
                </div>
                <div className="form-group">
                    <label>Age</label>
                    <input ref={register} type="number" name="age" min="18" max="120" className="form-control" placeholder="Your Age *" required="required" data-validation-required-message="Please enter your age"/>
                    <p class="help-block text-danger"></p>
                    {errors.age && <p>{errors.age.message}</p>}
                </div>
                <div className="form-group">
                    <label>Email</label>
                    <input ref={register} type="email" name="email" pattern="/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/" className="form-control" placeholder="Your Email *" required="required" data-validation-required-message="Please enter your email"/>
                    <p class="help-block text-danger"></p>
                    {errors.email && <p>{errors.email.message}</p>}
                </div>
                <div className="form-group">
                    <label>File Upload</label>
                    <input ref={register} type="file" name="file" className="form-control" />
                    {errors.file && <p>{errors.file.message}</p>}
                </div>
                <button type="submit" className="btn btn-primary btn-block">Submit</button>
            </form>
        </div>
        </section>
    )
}

 export default Contact;